
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import commonBtn from '@/components/commonBtn'
export default {
name: 'updateTel',
components: {
XInput, XButton, commonBtn, Countdown
},
data () {
return {
codeBtnName: '获取',
codeBtnStatus: false,
time: 60,
ylTel: 13245697841
}
},
methods: {
next () {

this.codeBtnStatus = false
this.time = 60
},
setCode () {
this.codeBtnStatus = true
},
finish () {
this.codeBtnStatus = false
this.time = 60
}
},
filters:{
toTel: (value) => {
value = String(value);
let start = value.slice(0, 3)
let end = value.slice(-4)
return `${start}****${end}`
}
}
}



